<template>
    <div class="experience">
        <div class="search-status">
			<el-button size="small" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
			<span class="mainFontColor edit" @click="editFunc">{{ edit }}</span>
        </div>
        <vue-scroll>
            <div class="content" v-if="list" :class="{marginBtn:editSelect}">
            <el-table
					ref="multipleTable"
                    :data="list"
                    :empty-text="$L('暂无数据')"
                    style="width: 100%" class="table"
                    :show-header="false"
                    @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="25" v-if="editSelect">
                </el-table-column>
                <el-table-column>
                    <div slot-scope="scope">
						<p class="p"><span>
                            <template v-if="scope.row.typeFlag == 1">
                                {{ $L('消费') }}<br>
                            </template>
                            <template v-else-if="scope.row.typeFlag == 2">
                                {{ $L('赠送经验值') }}<br>
                            </template>
                        </span></p>
						<p class="p">
                            <span class="font-grey left desc">{{ scope.row.orderSn }}</span>
                            <span class="desc right details">
                                <template v-if="scope.row.typeFlag == 1">
                                    {{ $L(' 消费')}}{{ scope.row.orderAmount }}{{$L(' 赠送')}}{{scope.row.points}}{{$L(' 个经验值') }}
                                </template>
                                <template v-else-if="scope.row.typeFlag == 3">
                                    {{ $L('管理员手动变动会员分组') }}
                                </template>
                                <template v-else-if="scope.row.typeFlag == 2">
                                    {{ $L('初始化增加经验积分')}}{{ scope.row.points }}
                                </template>
                            </span>
                        </p>
						<p class="p"><span class="left">{{  scope.row.time }}</span><span class="font-green right ">+{{ scope.row.points }}</span></p>
                    </div>
                </el-table-column>
            </el-table>
            <div class="list-pages" v-if="list.length > 0">
                <el-pagination
					small
					background
					@current-change="handleCurrentChange"
					:current-page="currentPage"
					:page-size="pageSize"
					layout="prev, pager, next"
					:total="allNum">
                </el-pagination>
            </div>
        </div>
        </vue-scroll>
        <div class="botton-btn" v-if="editSelect">
           <div class="left">
               <el-checkbox v-model="checkAll" @change="handleCheckAllChange(list)">{{ $L('全选') }}</el-checkbox>
           </div>
            <div class="right">
                <el-button class="btn" type="primary" :disabled=disabled @click="delMsgFunc()">{{ $L('删除') }}</el-button>
            </div>
        </div>
		<transition name="fade">
			<div class="time" v-if="showTime">
				<div class="close" @click="closeTimeFunc"></div>
				<div class="date">
					<calendar></calendar>
                    <inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
                                    :weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
                                    @change="onChange" :defaultDate="defaultDate"></inlineCalendar>
					<div class="btns">
						<el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
						<el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
					</div>
				</div>
			</div>
		</transition>
    </div>
</template>

<script>
    export default {
        name: "recordMobile",
        data () {
            return {
                editSelect:false,
                msgArr:[],
                checkAll:false,
				disabled:true,
				dtCreateStart:'',
				dtCreateEnd:'',
				showTime:false,
				timeArr:[],
				defaultDate:[],
				edit:this.$L('编辑'),
            }
        },
        inject : ['reload'],
        props : {
            routerData : {
                type : Object,
                default:()=>{
                    return {};
                }
            }
        },
        computed : {
            list:{
                get(){
                    return this.routerData.hasOwnProperty('data') &&
                    this.routerData.data.hasOwnProperty('experList') ?this.routerData.data.experList.list:[];
                }
            },
            currentPage:{
                get(){
                    return this.routerData.hasOwnProperty('p')?this.routerData.p:1;
                }
            },
            pageSize:{
                get(){
                    return this.routerData.hasOwnProperty('num')?this.routerData.num:10;
                }
            },
            allNum:{
                get(){
                    return this.routerData.hasOwnProperty('data') &&  this.routerData.data.hasOwnProperty('experList') ?this.routerData.data.experList.allNum:0;
                }
            },
        },
        methods : {
            setupTopBtn() {
                this.mobileTopBtn = [
                    {
                        text: this.$L('编辑'),
                        size: '14px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.edit();
                        }
                    },
                ];
            },
            handleCurrentChange(val){
                this.$emit('action',{
                    action:'page',
                    value:val
                });
                this.checkAll = false;
            },
            handleSelectionChange(val) {
				if(val.length > 0){
					this.disabled = false;
				}else{
					this.disabled = true;
				}
                let arr = [];
                for (let i in val) {
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
                console.log(this.msgArr);
            },
            SearchTimeFunc(time){
                if(time !== null){
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                    this.$emit('action',{
                        action:'time',
                        value:{ dtCreateStart:this.dtCreateStart,dtCreateEnd:this.dtCreateEnd}
                    });
                }else{
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }

            },
            editFunc(){
                if(this.editSelect){
					this.edit = '编辑';
                    this.editSelect = false;
                } else {
					this.edit = '取消';
                    this.editSelect = true;
                }
            },
            delMsgFunc() {
                const _self = this;
                if ((Array.isArray(this.msgArr) && this.msgArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中经验记录，请勾选后再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'warning',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        if (action === 'cancel') {
                            _self.$router.push({name: 'home'});
                        } else {
                            _self.loading = false;
                            _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                        }
                    });
                    return;
                }

                let logIds;

                logIds = this.msgArr;

                let arr = {
                    logIds: logIds
                }
                console.log(arr);
                _self.$confirm(_self.$L('此操作将永久删除该经验记录, 是否继续?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deleteExperienceList', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.reload();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'userCardMobile'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'userCardMobile'});
                        });
                    });
                }).catch(() => {

                });
            },
            //选择全部
            handleSelectionALL(){
                let arr = [];
                for (let i in this.tableData.list){
                    arr[i] = this.tableData.list[i].id;
                }
                this.allArr = arr;
            },
            //全选
            handleCheckAllChange(rows){
            	if (rows) {
            	    rows.forEach(rows => {
            	        this.$refs.multipleTable.toggleRowSelection(rows, !this.allSelect)
            	    })
            	    this.allSelect = !this.allSelect;
            	
            	    let arr = [];
            	    if (this.allSelect) {
            	        for (let i in rows){
            	            arr[i] = rows[i].id;
            	        }
            	        this.msgArr = arr;
            			this.disabled = false;
            	    } else {
            	        this.msgArr = arr;
            			this.disabled = true;
            	    }
            	}
            },
			showTimeFunc(){
				this.showTime = !this.showTime;
			},
			onChange(date) {
				let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
				arr = arr.replace(/"|\[|\]/g,'');
				arr = arr.split(',');
				this.defaultDate = arr;
				this.dtCreateStart = arr[0];
				this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
			},
			closeTimeFunc(){
				this.showTime = false;
				this.defaultDate = [];
				this.dtCreateStart = '';
				this.dtCreateEnd = '';
				this.$emit('action',{
				    action:'time',
				    value:{ dtCreateStart:this.dtCreateStart,dtCreateEnd:this.dtCreateEnd}
				});
			},
			gotoTime(){
				this.showTime = false;
				this.$emit('action',{
				    action:'time',
				    value:{ dtCreateStart:this.dtCreateStart,dtCreateEnd:this.dtCreateEnd}
				});
			},
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../../assets/css/common/common.scss";
    .experience{
        position: absolute;
        left: 0;
        top: 0;
        .search-status{
			position: relative;
            height: 50px;
			line-height: 50px;
            border-bottom: 1px solid #F2F6FC;
            background: #fff;
			padding: 0 10px;
			.edit{
				position: absolute;
				right: 10px;
				font-size: 14px;
			}
        }
        .content{
            width: 100%;
            height: 100%;
            margin: 10px 0;
            margin-bottom: 60px;
            &.marginBtn{
                margin-bottom: 110px;
            }
            .table{
                font-size: 12px;
				/deep/ .el-table__row{
					.p{
						line-height: 24px;
						height: 24px;
						.left{
							float: left;
						}
						.right{
							float: right;
						}
					}
				}
                .font-grey{
                    color: #999;
                }
                .details{
                    padding-left: 5px;
                    text-align: right;
                }
                .desc{
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    display: inline-block;
                    width: 50%;
                }
                .font-green{
                    color: #00bf00;
                    font-weight: 700;
                }
            }
            .list-pages{
                background: #fff;
                text-align: center;
                padding: 20px 0;
                margin-top: 10px;
            }
        }
        .botton-btn{
            position: fixed;
            bottom: 0;
            width: 100%;
            height: 50px;
            background: #fff;
            z-index: 100;
            border-top: 1px solid #F2F6FC;
            .left{
                display: inline-block;
                line-height: 50px;
                float: left;
                padding-left: 15px;
            }
            .right{
                display: inline-block;
                height: 50px;
                line-height: 50px;
                float: right;
                .btn{
                    height: inherit;
                    width: 120px;
                }
            }
        }
		.time{
			height: 100%;
			position: fixed;
			z-index: 1000;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.6);
			.date{
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				.btns{
					background-color: #fff;
					text-align: right;
					padding: 5px 10px;
				}
			}
			.close{
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
			}
		}
		.fade-enter,
		.fade-leave-to{
		   opacity: 0;
		}
		.fade-enter-active,
		.fade-leave-active{
		   transition: opacity 0.5s
		}
    }
</style>
<style>
    .el-message-box {
        width: 94% !important;
    }
</style>